import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import NFTDevelopmentScreen from "../screens/nft-development-company/nft-development-screen";

const NFTDevelopmentCompany = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "NFT Marketplace Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <NFTDevelopmentScreen db={db} />
      </main>
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="NFT Marketplace Development Company | Justtry Technologies"
    headTitle="NFT Marketplace Development Company | NFT Software Development"
    description="Justtry is a top-end NFT Marketplace Development Company that offers the best NFT Marketplace Development solutions and Services to Create an NFT Marketplace."
    keywords="Non-fungible token marketplace development, NFT marketplace development, NFT marketplace platform development, NFT marketplace development company, NFT marketplace app development, How to create an NFT marketplace, NFT marketplace development solutions, Create your own NFT marketplace, NFT marketplace software development, White label NFT marketplace development, NFT marketplace development services"
  ></Seo>
);
export default NFTDevelopmentCompany;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
